.card {
  margin: 8px;
  background: rgb(253, 253, 253);
  width: 40%;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 0px 4px gray;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  align-items: center;
}

.list {
  list-style: none;
  display: flex;
  flex-direction: column;
  margin-left: -35px;
  justify-content: center;
  text-align: center;
}
.listItem {
  margin: 2px;
  color: #444;
  background: rgb(238, 235, 235);
  border-radius: 8px;
  padding: 2px;
}
.button {
  background: #fefefe;
  color: rgb(49, 86, 130);
  font-size: 1.2em;
  padding: 8px 20px;
  border-radius: 8px;
  border: 2px solid rgb(46, 96, 130);
  outline: none;
  transition: ease all 0.4s;
  margin-top: 20px;
}

@media only screen and (max-width: 970px) {
  .card {
    width: 50%;
  }
}
@media only screen and (max-width: 768px) {
  .card {
    width: 300px;
  }
}
