.loginForm {
  width: 360px;
  margin: 20px auto;
  box-sizing: border-box;
}
.input {
  width: 90%;
}
.label {
  color: #ffffff;
  font-size: 1.4em;
}
.inputField {
  width: 100%;
  height: 30px;
  border: #9f9f9f 1px solid;
  outline: none;
  border-radius: 2px;
  padding: 8px 12px;
  margin: 8px 0;
  border-radius: 8px;
}
.inputField::placeholder {
  font-size: 1.2em;
}
.inputField:focus {
  border-color: #4f4f4f;
}
@media only screen and (max-width: 768px) {
  .loginForm {
    width: 300px;
  }
}
